.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;

}

.dialogsItems {
    padding: 10px;
    

}

.dialogsItems a {

    color: #285473;
    text-decoration: none;
    transition: color 0.2s;
}

.dialogsItems a:hover {

    font-weight: bold;
    color: indianred;
}


.dialogsItems .active {
    color: gold;

}


.dialogBlock {
    position: relative;
    
    display: block;
    margin-bottom: 20px;
}

.dialogName {
    position: absolute;
    left: 58px;
    top: 14px;
    text-align: left;
    
}


.dialogAvatar {
    margin-right: 5px;
    width: 50px;
    height: 50px;
    border-radius: 50%;

}


.messages {
    padding: 10px;

}

.message {

    color: #131313;
    text-decoration: none;
    padding-top: 10px;

}