.messageFormWrapper {
    margin-top: 30px;
}


.textArea {
    resize: none;
    min-width: 45%;
    min-height: 50px;
    border: none;
    padding: 15px 15px;
    background-color: #fff;
    font-size: 18px;
    transition: all 0.2s;

}

.textArea:focus {
    outline: none;
    background: #fcfcfc;
}


.btn {
    display: block;
    margin-top: 5px;
    width: 130px;
    height: 50px;
    border: none;
    background: #F2856D;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: bold;
    transition: all 0.2s;


}

.btn:hover, .btn:focus {

    background: #ebcfab;
    outline: none;
}